import React, { Component } from 'react';
import { Card, CardBody, CardHeader, Col, Row, Table } from 'reactstrap';
import HospitalAdmission from '../QrCode/QrCode';
import {getAdmission,getRoom,getUser,getWard,getHospital,getDepartment, getBed, getUserRole} from './../Apis.js'
//import admissionsData from './admissionsData'

class Admission extends Component {

  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: [],
      showHospitalInfo:false
    };
  }

  componentDidMount() {
    console.log(this.props.match.params.id)
    this.getAdmission(this.props.match.params.id)
  
  }
  getAdmission=async(id)=>{
    console.log(id,'asdf')
    const responseData= await getAdmission(this.props.match.params.id);
    console.log(responseData)
      if(responseData.status == 200){
        this.setState({
           
          items: responseData.data
        });
        this.getDepartment( responseData.data.depart_id)
        this.getRoom( responseData.data.room_id)
        this.getWard( responseData.data.ward_id)
        this.getBed(responseData.data.bed_id)
        this.getUser( responseData.data.patient_id,'creator')
        // this.getUser( responseData.data.modified_by,'Modifier')
        this.getHospital( responseData.data.hospital_id)
      } else if (responseData.status == 404) {
        console.warn(responseData.data.message);
      } 
  }
  getDepartment=async(id)=>{
    const responseData= await getDepartment(id);
    console.log(responseData,'response')
      if(responseData.status == 200){
        this.setState({
          
          departName: responseData.data.name
            
        })
      } else if (responseData.status == 404) {
        console.warn(responseData.data.message);
      } 
  }
  getBed=async(id)=>{
    const responseData= await getBed(id);
    console.log(responseData,'response')
      if(responseData.status == 200){
        this.setState({
          
          BedName: responseData.data.bed_no
            
        })
      } else if (responseData.status == 404) {
        console.warn(responseData.data.message);
      } 
  }
  getWard=async(id)=>{
    const responseData= await getWard(id);
    console.log(responseData,'response')
      if(responseData.status == 200){
        this.setState({
          wardName: responseData.data.name 
        })
      } else if (responseData.status == 404) {
        console.warn(responseData.data.message);
      } 
  }
  getRoom=async(id)=>{
    const responseData= await getRoom(id);
    console.log(responseData,'response')
      if(responseData.status == 200){
        this.setState({
          
          roomName: responseData.data.room_no
            
        })
      } else if (responseData.status == 404) {
        console.warn(responseData.data.message);
      } 
  }
  getUser=async(id,user)=>{
    
    const responseData= await getUserRole(id);
    console.log(responseData,id,'response in creator')
      if(user=='creator')
      {
        if(responseData.status==200)
          {
            this.setState({
              isLoaded: true,
              patientName: responseData.data.user.first_name+' '+ responseData.data.user.last_name
            });
           
         
        }
        else{
          this.setState({
            creatorName:'Creator has been removed'
        });
        }
       
      }
      else if(user=='Modifier'){
        
        if(responseData.status==200)
        {
          this.setState({
            modifierName: responseData.data.user.first_name+' '+ responseData.data.user.last_name
          });
        }
        else{
          this.setState({
            modifierName:'Not yet modified by any user'
          });
         
        }
      }
      
      
  }
  getHospital=async(id)=>{
    const responseData= await getHospital(id);
    console.log(responseData,'response')
      if(responseData.status == 200){
        this.setState({
          hospitalName: responseData.data.name
        })
      } else if (responseData.status == 404) {
        console.warn(responseData.data.message);
      } 
  }

  showHospitalInfo=()=>{
    this.setState({showHospitalInfo:true})
  }
  render() {

    const admission = this.state.items; //admissionsData.find( admission => admission.id.toString() === this.props.match.params.id)

    let roleName = '-';

    if(admission.roles !== undefined && admission.roles.length !== 0){
      roleName = admission.roles[0].name;
    }

    return (
      <div className="animated fadeIn">
        <Row>
          <Col lg={3}>
          </Col>
          <Col lg={6}>
            <Card>
              <CardHeader>
                <strong><i className="icon-info pr-1"></i>Admission id: {this.props.match.params.id}</strong>
                {/* <button onClick={this.showHospitalInfo} className='btn btn-success  admissionbtn'>Enter Admission Info</button> */}
              </CardHeader>
              <CardBody>
              
                  <Table responsive striped hover>
                  <tbody>                    
                    <tr>
                      <td>Admission No:</td>
                      <td><strong>{admission.id}</strong></td>
                    </tr>
                    <tr>
                      <td>Patient Name By:</td>
                      <td><strong>{this.state.patientName}</strong></td>
                    </tr>
                    <tr>
                      <td>Hospital Name:</td>
                      <td><strong>{this.state.hospitalName}</strong></td>
                    </tr>
                    
                    <tr>
                      <td>Room No:</td>
                      <td><strong>{this.state.roomName}</strong></td>
                    </tr>
                    <tr>
                      <td>Ward Name:</td>
                      <td><strong>{this.state.wardName}</strong></td>
                    </tr>
                    <tr>
                      <td>Department Name:</td>
                      <td><strong>{this.state.departName}</strong></td>
                    </tr>
                    <tr>
                      <td>Bed No:</td>
                      <td><strong>{this.state.BedName}</strong></td>
                    </tr>
                    
                    {/* <tr>
                      <td>Modified By:</td>
                      <td><strong>{this.state.modifierName}</strong></td>
                    </tr> */}
               
                    <tr>
                      <td>Status:</td>
                      <td><strong>{admission.status==1?'Enable':'Disable'}</strong></td>
                    </tr>
                    {/* <tr>
                      <td>Status Reason:</td>
                      <td><strong>{admission.status_reason}</strong></td>
                    </tr> */}
                  
                  
                
                  </tbody>
                </Table>
              
                 
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    )
  }
}

export default Admission;
